$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
	}

	.cols-#{$i} {
		columns: #{$i};
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}

		.cols-tablet-#{$i} {
			columns: #{$i};
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}

		.cols-mobile-#{$i} {
			columns: #{$i};
		}
	}
}

// Services
.wp-block-services-section-services .ssbServices {
	display: grid;

	.wp-block-services-section-service {
		height: 100%;
		max-width: 100%;
	}

	&.layout1 {
		.wp-block-services-section-service:hover {
			.ssbService {
				transform: translateY(0);
			}
		}
	}
}

// Service
.wp-block-services-section-service {
	* {
		box-sizing: border-box;
	}

	.ssbService {
		height: 100%;
		position: relative;
		transition: all .5s ease;

		.serviceLink {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0px;
			bottom: 0px;
		}

		.bgLayer {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0px;
			bottom: 0px;
			opacity: 0;
			transition: all .5s ease;
			z-index: -1;
		}

		.icon {
			display: inline-block;
			line-height: initial;

			i {
				font-style: normal;
			}
		}

		a {
			text-decoration: none;
		}

		.description {
			margin-bottom: 15px;
		}

		.link {
			position: absolute;
			left: 50px;
			bottom: -23px;

			a {
				position: relative;
				display: inline-flex;
				width: 50px;
				height: 50px;
				align-items: center;
				justify-content: center;
				font-size: 20px;
				line-height: 50px;
				text-align: center;
				border-radius: 50%;
				transition: all .5s ease;
				z-index: 1;

				svg {
					display: flex;
					align-items: center;
					justify-content: center;
				}
			}
		}
	}

	&:hover {
		.ssbService {
			transform: translateY(-15px);
			background: none !important;

			.bgLayer {
				opacity: 1;
			}
		}
	}
}